<template>
    <div class="paymentWarp">
        <header>
            <router-link to="confiemorder">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <section>
                <span>在线支付</span>
            </section>
        </header>
        <section class="show_time_amount">
            <section>
                <div class="time_last">支付剩余时间</div>
                <p class="time">00:{{shi}}:{{miao}}</p>
            </section>
        </section>
        <div class="payment">选择支付方式</div>
        <section class="pay_way_list">
            <section class="pay_item">
                <div class="pay_icon_contaienr">
                    <div class="zhifubao">
                        <!--<img src="https://img.zcool.cn/community/01db1c5ad0dab9a8012138678d8db9.png@2o.png" alt="" style="width: 100%;height: 100%">-->
                    </div>
                    <span>支付宝</span>
                </div>
                <i class="el-icon-success" :style="10 == dian ? 'font-size: 1rem;color: #4cd964' : 'font-size: 1rem;color: #ccc'" @click="duiBtn(10)"></i>
                <!--<i class="el-icon-success" style="font-size: 1rem;color: #ccc" v-if="isShow1"></i>-->
            </section>
            <section class="pay_item">
                <div class="pay_icon_contaienr">
                    <div class="weixin">
                        <!--<img src="https://dss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1660502907,4077635410&fm=26&gp=0.jpg" alt="" style="height: 100%;width: 100%">-->
                    </div>
                    <span>微信</span>
                </div>
                <i class="el-icon-success" :style=" 20 == dian ? 'font-size: 1rem;color: #4cd964' : 'font-size: 1rem;color: #ccc'" @click="duiBtn1(20)"></i>
                <!--<i class="el-icon-success" style="font-size: 1rem;color: #4cd964" v-if="isShow3"></i>-->
            </section>
        </section>
        <div class="footer" @click="que_ren_zhi_fu_btn">确认支付</div>
        <!--弹窗1-->
        <div class="alet_container" v-if="isShowTan">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">暂不开放支付功能</p>
                <div class="confrim" @click="dianBtn">确认</div>
            </section>
        </div>
        <!--前往app的弹窗-->
        <div class="alet_container" v-if="isShowTan1">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">前往app付款</p>
                <div class="confrim" @click="qu_app_btn">确认</div>
            </section>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PayMent",
        data(){
            return {
                isShowTan:true,
                isShowTan1:false,
                shi:14,
                miao:59,

                dian: 10,

            }
        },
        mounted(){
            this.gettime();
        },
        methods:{
            dianBtn(){
                this.isShowTan = false;
            },
            //倒计时函数
            gettime(){
                let  clock = setInterval( ()=>  {
                    this.miao -=1;
                    if(this.miao<0){
                        this.miao=59;
                        this.shi --;
                        if(this.shi == 0){
                            this.miao=0;
                            clearInterval(clock)
                        }
                        if(this.shi=== 0&&this.miao=== 0){
                            alert('你没给钱小伙子!')
                        }
                    }
                },1000)
            },
            //对号
            duiBtn(a){
                this.dian = a;
            },
            duiBtn1(i){
                this.dian = i;
            },
            que_ren_zhi_fu_btn(){
                this.isShowTan1 = true
            },
            qu_app_btn(){
                this.$router.push({path:"/order"})
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id")
            this.cart_id = localStorage.getItem("gou_wu_che_id");
            this.shou_huo_id =JSON.parse(localStorage.getItem("addressInfo"));
            this.shop_id = localStorage.getItem("shopId");
            this.geohash = JSON.parse(localStorage.getItem("addressInfo"));
            // this.description = JSON.parse(localStorage.getItem("addressInfo"));
            this.entities = JSON.parse(localStorage.getItem("entitiesArr"));
            console.log(this.entities)

            this.axios.post("/v1/users/"+this.user_id +"/carts/"+  this.cart_id + "/orders",{
                address_id:this.shou_huo_id[0].id,
                restaurant_id:this.shop_id,
                geohash:this.geohash[0].st_geohash,
                    // description: this.description[0].
                        entities:this.entities
            }).then(res=>{
                console.log(res)
            }).catch(err=>{})
        }
    }
</script>

<style scoped>
    .paymentWarp{
        padding-top: 1.95rem;
        /*background-color: azure;*/
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    header>a{
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }
    header>section{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    header>section span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }

    .show_time_amount{
        background-color: #fff;
        padding: .7rem;
        text-align: center;
    }
    .time_last{
        font-size: .6rem;
        color: #666;
        margin-top: 1rem;
    }
    .time{
        font-size: 1.5rem;
        color: #333;
        margin: .5rem 0 1rem;
    }
    .payment{
        background-color: #f1f1f1;
        padding: 0 .7rem;
        font-size: .7rem;
        color: #666;
        line-height: 1.8rem;
    }

    .pay_way_list{
        background-color: #fff;
    }
    .pay_item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: .4rem .7rem;
        line-height: 2.6rem;
        border-bottom: .025rem solid #f5f5f5;
    }
    .pay_icon_contaienr{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .zhifubao{
        width: 2rem;
        height: 2rem;
        background-size: 100% 100%;
        margin-right: .2rem;
        border-radius: 1rem;
        background-color: red;
    }
    .pay_icon_contaienr span{
        font-size: .7rem;
        color: #666;
    }
    .weixin{
        width: 2rem;
        height: 2rem;
        background-size: 100% 100%;
        margin-right: .2rem;
        background-color: red;
        border-radius: 1rem;
    }
    .footer{
        background-color: #4cd964;
        font-size: .7rem;
        color: #fff;
        text-align: center;
        margin: 0 .7rem;
        line-height: 1.8rem;
        border-radius: .2rem;
        margin-top: .5rem;
        font-weight: 700;
    }


    .alet_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon>:nth-child(1){
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon>:nth-child(2){
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }

</style>